import React, { useEffect, useMemo, useState } from 'react';
import {
  Activity,
  AlertTriangle,
  CheckCircle2,
  Cpu,
  Loader2,
  Lock,
  RefreshCw,
  Shield,
  SlidersHorizontal,
  Sparkles,
  ToggleLeft,
  ToggleRight
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPost } from '../../utils/api';

type HybridStrategy = 'weighted' | 'majority' | 'unanimous' | 'union';
type HybridMethod = 'rule_based' | 'pattern_based' | 'llm_based';

interface HybridConfig {
  enabled: boolean;
  strategy: HybridStrategy;
  methods: HybridMethod[];
  hide_metadata: boolean;
  auto_enable_for_tiers: string[];
}

interface HybridStats {
  total_analyses: number;
  hybrid_analyses: number;
  hybrid_percentage: number;
  avg_methods_used: number;
  strategy_distribution: Record<string, number>;
}

const STRATEGY_OPTIONS: Array<{
  value: HybridStrategy;
  title: string;
  description: string;
}> = [
  {
    value: 'weighted',
    title: 'ממוצע משוקלל',
    description: 'משקל דינמי לכל מנוע לפי דיוק היסטורי ועלות.'
  },
  {
    value: 'majority',
    title: 'רוב קולות',
    description: 'הסתירה מתקבלת רק אם רוב המנועים מסכימים.'
  },
  {
    value: 'unanimous',
    title: 'קונצנזוס מלא',
    description: 'נדרש אישור מכל המנועים — רגישות גבוהה, על חשבון כיסוי.'
  },
  {
    value: 'union',
    title: 'איחוד תוצאות',
    description: 'מאחדים את כל התוצאות (מתאים למחקר ראשוני).'
  }
];

const METHOD_OPTIONS: Array<{
  value: HybridMethod;
  title: string;
  description: string;
}> = [
  {
    value: 'rule_based',
    title: 'חוקים דטרמיניסטיים',
    description: 'חוקים ידניים לאיתור קונפליקטים ברורים ומהירים.'
  },
  {
    value: 'pattern_based',
    title: 'דפוסים סטטיסטיים',
    description: 'דגימת דפוסים לשוניים וסמנטיים לאיתור סתירות נדירות.'
  },
  {
    value: 'llm_based',
    title: 'מודלי שפה',
    description: 'ניתוח עמוק בעזרת LLMs למציאת קשרים חבויים.'
  }
];

const TIER_LABELS: Record<string, string> = {
  free: 'Free',
  basic: 'Basic',
  professional: 'Professional',
  enterprise: 'Enterprise',
  admin: 'Admin'
};

const HybridControlSettings: React.FC = () => {
  const [config, setConfig] = useState<HybridConfig | null>(null);
  const [baselineConfig, setBaselineConfig] = useState<HybridConfig | null>(null);
  const [stats, setStats] = useState<HybridStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [refreshing, setRefreshing] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);

  useEffect(() => {
    loadHybridData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const loadHybridData = async (showToast = false) => {
    try {
      if (!refreshing) {
        setLoading(true);
      }
      setError(null);

      const [configResponse, statsResponse] = await Promise.all([
        apiGet('/api/admin/hybrid/config', { showErrorToast: false }),
        apiGet('/api/admin/hybrid/stats', { showErrorToast: false })
      ]);

      setConfig(configResponse);
      setBaselineConfig(configResponse);
      setStats(statsResponse);
      setLastUpdated(new Date());
      if (showToast) {
        toast.success('ההגדרות רועננו בהצלחה');
      }
    } catch (err: any) {
      const message = err?.message || 'שגיאה בטעינת הגדרות Hybrid';
      setError(message);
      toast.error(message);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  const isDirty = useMemo(() => {
    if (!config || !baselineConfig) return false;
    const strategyChanged = config.strategy !== baselineConfig.strategy;
    const methodsChanged =
      config.methods.slice().sort().join(',') !== baselineConfig.methods.slice().sort().join(',');
    return strategyChanged || methodsChanged;
  }, [config, baselineConfig]);

  const handleToggleHybrid = async () => {
    if (!config) return;
    const nextEnabled = !config.enabled;
    setSaving(true);
    try {
      await apiPost(`/api/admin/hybrid/${nextEnabled ? 'enable' : 'disable'}`, {}, { showErrorToast: false });
      const updatedConfig = { ...config, enabled: nextEnabled };
      setConfig(updatedConfig);
      setBaselineConfig((prev) => (prev ? { ...prev, enabled: nextEnabled } : updatedConfig));
      toast.success(nextEnabled ? 'Hybrid הופעל לכל הניתוחים' : 'Hybrid הושבת');
    } catch (err: any) {
      toast.error(err?.message || 'שגיאה בעדכון סטטוס Hybrid');
    } finally {
      setSaving(false);
    }
  };

  const handleStrategyChange = (value: HybridStrategy) => {
    setConfig((prev) => (prev ? { ...prev, strategy: value } : prev));
  };

  const toggleMethod = (method: HybridMethod) => {
    setConfig((prev) => {
      if (!prev) return prev;
      const exists = prev.methods.includes(method);
      if (exists && prev.methods.length === 1) {
        toast.error('חייב להיות לפחות מנגנון אחד פעיל');
        return prev;
      }
      const methods = exists
        ? prev.methods.filter((m) => m !== method)
        : [...prev.methods, method];
      return { ...prev, methods };
    });
  };

  const handleSave = async () => {
    if (!config || saving || !isDirty) return;
    setSaving(true);
    try {
      await apiPost(
        '/api/admin/hybrid/config',
        {
          strategy: config.strategy,
          methods: config.methods
        },
        { showErrorToast: false }
      );
      setBaselineConfig(config);
      toast.success('ההגדרות נשמרו בהצלחה');
    } catch (err: any) {
      toast.error(err?.message || 'שגיאה בשמירת ההגדרות');
    } finally {
      setSaving(false);
    }
  };

  const handleRefresh = async () => {
    setRefreshing(true);
    await loadHybridData(true);
  };

  if (loading && !config) {
    return (
      <div className="p-8 flex items-center justify-center min-h-[60vh]">
        <div className="text-center text-blue-200">
          <Loader2 className="w-12 h-12 mx-auto mb-4 animate-spin" />
          <p>טוען הגדרות Hybrid...</p>
        </div>
      </div>
    );
  }

  if (!config) {
    return (
      <div className="p-8">
        <div className="bg-red-500/10 border border-red-500/40 rounded-2xl p-8 text-center">
          <AlertTriangle className="w-12 h-12 text-red-400 mx-auto mb-4" />
          <p className="text-white text-lg font-semibold mb-2">לא ניתן לטעון את ההגדרות</p>
          <p className="text-red-200 mb-4">{error || 'נסה לרענן את העמוד'}</p>
          <button
            onClick={handleRefresh}
            className="px-6 py-2 bg-red-500/80 hover:bg-red-500 text-white rounded-lg transition"
          >
            רענן ונסה שוב
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6" dir="rtl">
      <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex flex-wrap items-center justify-between gap-4">
        <div>
          <h1 className="text-3xl font-bold text-white flex items-center gap-3">
            <Shield className="text-cyan-400" />
            בקרה על Hybrid Detection
          </h1>
          <p className="text-blue-200 mt-1">
            נהל את תצורת זיהוי הסתירות ההיברידי ואת מנגנוני האנסמבל
          </p>
          {lastUpdated && (
            <p className="text-xs text-slate-400 mt-2">
              עודכן לאחרונה: {lastUpdated.toLocaleTimeString('he-IL')}
            </p>
          )}
        </div>

        <div className="flex gap-3">
          <button
            onClick={handleRefresh}
            disabled={refreshing}
            className="flex items-center gap-2 px-4 py-2 bg-white/10 border border-white/20 text-white rounded-lg hover:bg-white/20 transition disabled:opacity-60"
          >
            <RefreshCw className={refreshing ? 'animate-spin' : ''} size={18} />
            רענן
          </button>
          <button
            onClick={handleToggleHybrid}
            disabled={saving}
            className={`flex items-center gap-2 px-5 py-2 rounded-lg font-semibold transition text-white ${
              config.enabled
                ? 'bg-emerald-600 hover:bg-emerald-500'
                : 'bg-slate-700 hover:bg-slate-600'
            } disabled:opacity-60`}
          >
            {config.enabled ? (
              <>
                <ToggleRight size={18} />
                Hybrid פעיל
              </>
            ) : (
              <>
                <ToggleLeft size={18} />
                Hybrid כבוי
              </>
            )}
          </button>
          <button
            onClick={handleSave}
            disabled={!isDirty || saving}
            className={`flex items-center gap-2 px-5 py-2 rounded-lg font-semibold transition ${
              isDirty
                ? 'bg-purple-600 hover:bg-purple-500 text-white'
                : 'bg-white/10 text-white/60 cursor-not-allowed'
            }`}
          >
            <CheckCircle2 size={18} />
            שמור שינויים
          </button>
        </div>
      </div>

      {error && (
        <div className="bg-red-500/10 border border-red-500/40 rounded-xl p-4 text-red-200 flex items-center gap-3">
          <AlertTriangle />
          {error}
        </div>
      )}

      {/* Status summary */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <StatusCard
          title="מצב Hybrid"
          value={config.enabled ? 'פעיל לכל הניתוחים' : 'כבוי'}
          accent={config.enabled ? 'text-emerald-300' : 'text-slate-300'}
          icon={config.enabled ? <ToggleRight className="text-emerald-400" /> : <ToggleLeft className="text-slate-400" />}
          description={config.enabled ? 'תיקי Tier מורשים ירוצו עם Hybrid אוטומטית' : 'המערכת משתמשת בזיהוי סטנדרטי בלבד'}
        />
        <StatusCard
          title="אסטרטגיית אנסמבל"
          value={STRATEGY_OPTIONS.find((opt) => opt.value === config.strategy)?.title || 'לא הוגדר'}
          accent="text-blue-300"
          icon={<SlidersHorizontal className="text-blue-400" />}
          description={STRATEGY_OPTIONS.find((opt) => opt.value === config.strategy)?.description || ''}
        />
        <StatusCard
          title="מנועים פעילים"
          value={`${config.methods.length} מנגנונים`}
          accent="text-cyan-300"
          icon={<Sparkles className="text-cyan-400" />}
          description={config.methods.map((method) => METHOD_OPTIONS.find((opt) => opt.value === method)?.title).join(' • ')}
        />
      </div>

      {/* Methods selection */}
      <section className="bg-white/5 border border-white/10 rounded-2xl p-6">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h2 className="text-2xl font-semibold text-white mb-1">מנועי זיהוי פעילים</h2>
            <p className="text-slate-300 text-sm">בחר אילו מנגנונים ישתתפו בכל ניתוח Hybrid</p>
          </div>
          <span className="text-xs text-slate-400">
            חייבים להשאיר לפחות אחד פעיל
          </span>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {METHOD_OPTIONS.map((option) => {
            const active = config.methods.includes(option.value);
            return (
              <button
                key={option.value}
                onClick={() => toggleMethod(option.value)}
                className={`text-right h-full p-4 rounded-2xl border transition ${
                  active
                    ? 'border-emerald-400 bg-emerald-500/10 text-white shadow-lg shadow-emerald-500/10'
                    : 'border-white/10 bg-white/5 text-slate-300 hover:bg-white/10'
                }`}
              >
                <div className="flex items-center justify-between mb-2">
                  <h3 className="text-lg font-semibold">{option.title}</h3>
                  {active ? (
                    <CheckCircle2 className="text-emerald-400" />
                  ) : (
                    <Lock className="text-slate-500" size={18} />
                  )}
                </div>
                <p className="text-sm text-slate-300 leading-relaxed">{option.description}</p>
              </button>
            );
          })}
        </div>
      </section>

      {/* Strategy options */}
      <section className="bg-white/5 border border-white/10 rounded-2xl p-6">
        <div className="flex items-center justify-between mb-6">
          <div>
            <h2 className="text-2xl font-semibold text-white mb-1">אסטרטגיית אנסמבל</h2>
            <p className="text-slate-300 text-sm">קובע כיצד מאחדים את תוצאות המנועים</p>
          </div>
        </div>
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-4">
          {STRATEGY_OPTIONS.map((option) => {
            const selected = config.strategy === option.value;
            return (
              <button
                key={option.value}
                onClick={() => handleStrategyChange(option.value)}
                className={`text-right p-4 rounded-2xl border transition h-full ${
                  selected
                    ? 'border-purple-400 bg-purple-500/10 text-white shadow-lg shadow-purple-500/10'
                    : 'border-white/10 bg-white/5 text-slate-300 hover:bg-white/10'
                }`}
              >
                <div className="flex items-center justify-between mb-2">
                  <h3 className="text-lg font-semibold">{option.title}</h3>
                  {selected && <CheckCircle2 className="text-purple-300" />}
                </div>
                <p className="text-sm text-slate-300 leading-relaxed">{option.description}</p>
              </button>
            );
          })}
        </div>
      </section>

      {/* Metadata + tier info */}
      <section className="grid grid-cols-1 lg:grid-cols-2 gap-4">
        <div className="bg-white/5 border border-white/10 rounded-2xl p-6">
          <h3 className="text-xl font-semibold text-white mb-3">חשיפת מטא-נתונים</h3>
          <p className="text-slate-300 text-sm mb-4">
            שדה זה מוגדר בבאקאנד כדי למנוע חשיפת מקור הסתירה למשתמש הקצה.
          </p>
          <div className="flex items-center gap-3">
            <Lock className="text-yellow-400" />
            <div>
              <p className="text-white font-medium">
                {config.hide_metadata ? 'מטא-נתונים נסתרים מהמשתמשים' : 'מטא-נתונים מוצגים בממשק'}
              </p>
              <p className="text-slate-400 text-sm">
                ניתן לעדכן ערך זה דרך קובץ התצורה או API ייעודי בלבד.
              </p>
            </div>
          </div>
        </div>

        <div className="bg-white/5 border border-white/10 rounded-2xl p-6">
          <h3 className="text-xl font-semibold text-white mb-3">תיקים עם הפעלה אוטומטית</h3>
          <p className="text-slate-300 text-sm mb-4">
            רשימת ה-Tier של משתמשים שמקבלים Hybrid באופן אוטומטי גם כשהמצב הכללי כבוי.
          </p>
          <div className="flex flex-wrap gap-2">
            {config.auto_enable_for_tiers.length === 0 && (
              <span className="px-3 py-1 rounded-full bg-white/10 text-slate-300 text-sm">
                אין Tier מוגדרים
              </span>
            )}
            {config.auto_enable_for_tiers.map((tier) => (
              <span
                key={tier}
                className="px-3 py-1 rounded-full bg-cyan-500/20 border border-cyan-500/40 text-sm text-cyan-200"
              >
                {TIER_LABELS[tier] || tier}
              </span>
            ))}
          </div>
        </div>
      </section>

      {/* Stats */}
      {stats && (
        <section className="bg-white/5 border border-white/10 rounded-2xl p-6 space-y-6">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-2xl font-semibold text-white mb-1">ביצועים וסטטיסטיקות</h2>
              <p className="text-slate-300 text-sm">התפלגות שימוש ב-Hybrid בחודש האחרון</p>
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <StatsCard
              title="סה״כ ניתוחים"
              value={stats.total_analyses.toLocaleString('he-IL')}
              icon={<Activity className="text-blue-300" />}
              accent="text-blue-200"
            />
            <StatsCard
              title="ניתוחי Hybrid"
              value={stats.hybrid_analyses.toLocaleString('he-IL')}
              icon={<Cpu className="text-purple-300" />}
              accent="text-purple-200"
            />
            <StatsCard
              title="שיעור Hybrid"
              value={`${stats.hybrid_percentage.toFixed(1)}%`}
              icon={<Shield className="text-emerald-300" />}
              accent="text-emerald-200"
            />
            <StatsCard
              title="מנועים ממוצעים"
              value={stats.avg_methods_used.toFixed(1)}
              icon={<Sparkles className="text-pink-300" />}
              accent="text-pink-200"
            />
          </div>

          <div>
            <h4 className="text-white font-semibold mb-3">התפלגות אסטרטגיות</h4>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-3">
              {Object.entries(stats.strategy_distribution).map(([strategy, count]) => (
                <div key={strategy} className="bg-white/5 border border-white/10 rounded-xl p-4">
                  <p className="text-slate-300 text-sm mb-1">
                    {STRATEGY_OPTIONS.find((opt) => opt.value === strategy)?.title || strategy}
                  </p>
                  <p className="text-2xl text-white font-bold">{count}</p>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}
      </div>
    </div>
  );
};

interface StatusCardProps {
  title: string;
  value: string;
  description: string;
  icon: React.ReactNode;
  accent: string;
}

const StatusCard: React.FC<StatusCardProps> = ({ title, value, description, icon, accent }) => (
  <div className="bg-white/5 border border-white/10 rounded-2xl p-4 flex items-start gap-3">
    <div className="p-3 rounded-xl bg-white/5 border border-white/10">{icon}</div>
    <div>
      <p className="text-slate-300 text-sm mb-1">{title}</p>
      <p className={`text-xl font-semibold ${accent}`}>{value}</p>
      <p className="text-slate-400 text-xs mt-1">{description}</p>
    </div>
  </div>
);

interface StatsCardProps {
  title: string;
  value: string;
  icon: React.ReactNode;
  accent: string;
}

const StatsCard: React.FC<StatsCardProps> = ({ title, value, icon, accent }) => (
  <div className="bg-white/5 border border-white/10 rounded-2xl p-4 text-right">
    <div className="flex items-center justify-between text-sm text-slate-300 mb-2">
      <span>{title}</span>
      {icon}
    </div>
    <p className={`text-3xl font-semibold ${accent}`}>{value}</p>
  </div>
);

export default HybridControlSettings;


